@charset "utf-8";
/* 메인 배너용 CSS - ban.css */
.banner {
  position: relative;
  overflow: hidden;
}

/* 배너슬라이드  */
.slider {
  position: relative;
  left: 0;
  display: flex;
}

.slider li {
  position: relative;
  /* 부모자격 */

  width: 100%;

  flex-shrink: 0;
}

/*슬라이드 이미지 */
.slider img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

/* 배너 컨텐츠 버튼 */
.banimg {
  width: 100%;
  object-fit: contain;
  aspect-ratio: 1/0.45;
}

/* 이동버튼 */
.banner .abtn {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  width: 40px;
  height: 40px;
  border-radius: 50%;
  font-size: 30px;
  padding: 0;
  box-sizing: content-box;
  line-height: 40px;
  cursor: pointer;
}

.lb {
  left: 1vw;
}

.rb {
  right: 1vw;
}

/* 블릿 */
.indic {
  position: absolute;
  left: 50%;
  bottom: 3vh;
  transform: translateX(-50%);
  display: flex;
}

.indic li {
  width: 10px;
  height: 10px;
  border-radius: 50%;
  border: 2px solid #fff;
  margin: 0 5px;
  cursor: pointer;
  text-shadow: 0 0 2px #000;
}

.indic li.on {
  background-color: rgba(98, 95, 95, 0.8);
}

.slide-four.on .banimg {
  width: 50%;
  height: 50%;
  object-fit: contain;
}

.slide-four.on .slider {
  text-align: center;
  display: block;
}

.slide-four.on .inbx {
  /* position: absolute; */
  /* top: 20%; */
}

.slide-three.on .inbx {
  /* position: absolute; */
  top: 0;
}

.slide-three.on .logoletter h5 {
  background: linear-gradient(to bottom right, rgb(255 1 1) 0%, #fff 100%);
  font-family: Anton;
  font-size: 100px;
  -webkit-text-stroke: 2px #222;
  color: #fff;
  font-weight: 500;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}
