/* Layout css */

/* .root {
  max-width: 1500px;
} */

#main {
  position: relative;
}
.top {
  max-width: 1200px;
  padding: 15px 0;
  margin: 0 auto;
}

h1 {
  display: flex;
}

h1 img {
  vertical-align: middle;
}

.header {
  /* position: fixed; */
  position: relative;
  z-index: 9999;
  height: 80px;
  width: 100%;
  margin: 0 auto;
  background-color: #040404;
}
.gnb {
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
}

.gnb ul {
  display: flex;
  gap: 100px;
  justify-content: space-around;
}

.gnb li {
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  width: 150px;
  height: 50px;
  cursor: pointer;
  transform-style: preserve-3d;
  transition: all 0.5s;
}

.gnb li:hover {
  transform: translateY(0) rotateX(90deg);
}

.gnb li a {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  line-height: 50px;
  font-size: 20px;
  font-weight: 600;
  color: #fff;
}

.gnb li a:first-child {
  /* background-color: #fff; */
  transform: translateZ(25px);
}

.gnb li a:last-child {
  transform: translateY(25px) rotateX(-90deg);
}

/*******************************************
                  footer 
************************************************/

/* div.footer.on {
  position: relative;
} */

div.footer {
  width: 100%;
  position: fixed;
  bottom: 0;
  padding: 1vw 0;
  background-color: #0a0a0a;
  z-index: 2;
}

.foot {
  /* 질문 */
  height: 60px;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;

  text-align: center;

  color: #e2e2e2;
}

.foot address {
  font-style: normal;
}
