h2.subtit {
  /* #45d6e7 */
  -webkit-text-stroke: 2px rgba(25, 25, 25, 1);
  /* -webkit-text-stroke: 2px #45d6e7; */
  font-family: 'Arial', dotum, sans-serif;
  color: rgba(0, 0, 0, 0);
  font-style: italic;
  font-size: 80px;
  font-weight: 700;
  letter-spacing: 5px;
  margin-bottom: 1vw;
}

h2.title {
  opacity: 0;
}

.atitaction h2.title.on {
  font-size: 80px;
  letter-spacing: 1px;
  font-family: Anton;
  display: inline-block;
  /* opacity: 0; */
  width: 100%;
  animation: appear-text 0.2s linear 0.009s forwards;
}

/*****appear-text ********* */
@keyframes appear-text {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
/*****appear-text ********* */
.atitaction {
  position: relative;
  display: inline-block;
  /* width: 44%; */
  height: 121px;
}

.btitaction.on {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: inline-block;
  animation: hiding-text-ani 0.7s cubic-bezier(0, 0.8, 0.6, 0.8) 0.1s infinite;
}

@keyframes hiding-text-ani {
  0% {
    width: 0%;
    left: 0;
  }

  50% {
    background: #fef102d1;
    width: 100%;
    left: 0%;
  }

  100% {
    left: 100%;
    width: 0%;
  }
}

