/******** partner***********/

article.partnertitle {
  /* width: 46%; */
  flex-shrink: 0;
}

.partnerexplain {
  margin-top: 30px;
}

.partnerexplain .en {
  font-family: 'Arial', dotum, sans-serif;
  margin-bottom: 25px;
  line-height: 1.3;
  font-size: 30px;
  margin: 40px;
}

/* .partnerexplain .en.first {
  animation: blink 1s linear 0.5s infinite;
} */

.partnerexplain .en.second.on {
  opacity: 0;
  animation: blink 0.8s linear 0.2s infinite;
}

@keyframes blink {
  0% {
    color: transparent;
  }
  100% {
    color: #191919;
    opacity: 1;
  }
}

.partname {
  margin-top: 1vw;
}

.partnertrack {
  position: relative;
  /* outline: 1px solid black; */
  margin: 0 auto;
  margin-top: 2.2vw;
  text-align: center;
  display: flex;
  flex-direction: column;
  gap: 30px;
  overflow: hidden;
  width: 1200px;
  /* width: calc(20vw * 8); */
}

ul.partnerslide {
  position: relative;
  left: 101%;
  display: flex;
  /* width: calc(20vw * 8 * 2); */
  width: 200%;
  opacity: 0;
}

ul.partnerslide.on {
  position: relative;
  left: 101%;
  display: flex;
  opacity: 1;

  animation: scroll 30s linear infinite;
  /* width: calc(20vw * 8 * 2); */
  width: 200%;
}

ul.partnerslide li {
  flex: 1;
  height: 60px;
  margin-right: 60px;
}

ul.partnerslide img {
  width: 100%;
  height: 100%;
}

@keyframes scroll {
  /* 0% {
    left: 100%;
  } */
  100% {
    left: -202%;
    /* left: cal(350px * 8 * (-1)); */
  }
}

