#flipbx {
  overflow: hidden;
  height: 120px;
  width: 360px;
  margin-bottom: 1vw;
}

.flipbg {
  /* position: relative; */
  box-sizing: border-box;
  height: 120px;
  width: 360px;

  border-style: ridge;
  border-width: 6px;
  border-color: #191919;
}

.fliptit {
  text-align: center;
  /* margin: 5px; */
  color: #fff;
  font-size: 72px;
  /* letter-spacing: 1px; */
  font-family: Anton;
  /* display: inline-block; */
  height: 100%;
}

.flipbg.first {
  animation: show 5s linear infinite;
}

/* .flipbg {
  box-sizing: border-box;
  height: 120px;
} */

.flipbg.first {
  background: #4ec7f3;
}

.flipbg.second {
  background: #42c58a;
}
.flipbg.third {
  background: #dc143c;
}

@keyframes show {
  0% {
    margin-top: -360px;
  }

  5% {
    margin-top: -240px;
  }
  33% {
    margin-top: -240px;
  }
  38% {
    margin-top: -120px;
  }
  66% {
    margin-top: -120px;
  }
  71% {
    margin-top: 0px;
  }
  99.99% {
    margin-top: 0px;
  }
  100% {
    margin-top: -360px;
  }
}

