.firstletterbx {
  /* width: 100%; */
  text-align: center;
  color: #fff;
  /* height: calc(100vh - 180px); */
  box-sizing: border-box;
  /* padding: 5vw; */
   /* position: relative;
   top: 20%; */
}


.firstletterbx h2 {
  font-family: 'Arial', dotum, san-serif;
  font-size: 80px;
  font-weight: 600;
  margin-top: 1vw;
}

.firstletterbx h2:first-child {
  position: relative;
}

.firstletterbx h2:nth-child(2) {
  position: relative;
}

.underline {
  font-family: 'Caveat', dotum, san-serif;
  font-style: italic;
  font-weight: 400;
  margin: 0 0.4vw;
}

.underlineafter {
  position: absolute;
  top: 27%;
  left: 61%;
  width: 0;
  height: 4px;
  background-color: #fff;
  animation: underline 0.2s ease 0.2s forwards;
}

@keyframes underline {
  to {
    width: 280px;
  }
}

.commerce {
  padding-left: 20px;
}

.double {
  -webkit-text-stroke: 1px #7cfb4c;
  color: rgba(0, 0, 0, 0);
  font-style: italic;
}

.doubleafter {
  letter-spacing: 2px;
  position: absolute;
  top: 0;
  left: 61%;
  -webkit-text-stroke: 1px #7cfb4c;
  color: rgba(0, 0, 0, 0);
  font-style: italic;
  animation: double 0.6s ease-in 0.2s infinite;
}

@keyframes double {
  to {
    left: 60%;
  }
}

.arrow {
  margin: 0 1vw;
}

.influencer {
  text-align: left;
  position: relative;
  left: 29%;
  top: 74%;
  width: 206px;
  opacity: 0;
  height: 71px;
  animation: influncer 0.6s ease 0.6s infinite;
}

@keyframes influncer {
  to {
    opacity: 1;
  }
}

.focus {
  color: #fef102;
}

