@charset "utf-8";
/* 전체 페이지 공통 CSS




- index.html 페이지 전체에 적용됨! */
@import url('https://fonts.googleapis.com/css2?family=Josefin+Sans:wght@300&display=swap');
/* font-family: 'Josefin Sans', sans-serif;  영어*/

@import url('https://fonts.googleapis.com/css2?family=Anton&display=swap');
/* 영어 font-family: 'Anton', sans-serif; */

@import url('https://fonts.googleapis.com/css2?family=Caveat&display=swap');
/* font-family: 'Caveat', cursive; */

@import url('https://fonts.googleapis.com/css2?family=Nanum+Pen+Script&display=swap');
/*  한글 
font-family: 'Nanum Pen Script', cursive;
*/

html,
body,
ul,
ol,
dl,
dt,
dd,
p,
figure,
figcaption,
h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  padding: 0;
  font-weight: normal;
}

/* ul,ol */
ul,
ol {
  list-style: none;
}

body {
  overflow-x: hidden;
  margin: 0 auto;
  width: 100%;

  /* background-color: #040404; */
}

a {
  text-decoration: none;
  color: #222;
}

img {
  vertical-align: -4px;
}

/* ***************** head ***************/

/* .slide {
  position: relative;
  height: 100vh
  /* height: calc(100vh - 100px); 

  width: 100%;
} */

.inbx {
  /* max-width: 1500px; */
  max-width: 1350px;
  /* height: calc(100% - 180px); */
  margin: 0 auto;
  padding: 0 3vw;
  /* margin-top: 180px; */
  position: relative;
  top: 15%;
}

.cont {
  position: relative;
  /* height: 100vh; */
  height: 700vh;
}

#main {
  position: relative;
}

.slide {
  width: 100%;
  height: 100vh;
  position: fixed;
  /* position: relative; */
  top: 0;
  left: 0;
  /* height: 100vh; */
  padding: 0;
  overflow: hidden;
}

.slide-one {
  z-index: 14;
  background-color: #040404;
}

.slide-two {
  z-index: 12;
  /* background: #d35400;
   */
  background-color: #040404;
}

.slide-three {
  z-index: 10;
  background: #16a085;
}

.slide-three.on {
  z-index: 0;
  background: #fef100;
  position: relative;
}

.slide-four {
  z-index: 8;
  /* background: #2980b9; */
  background: #2c3e50;
}

.slide-four.on {
  z-index: 0;
  background: #fef100;
  position: relative;
}
.slide-four.bg {
  z-index: 0;
  position: relative;
}

.slide-five {
  z-index: 6;
  background: #45d6e7;
}

.slide-five.on {
  z-index: 0;
  position: relative;
} 


.slide-six {
  z-index: 4;
  /* background: #7f8c8d; */
  /* background: #d35400; */
  /* background: #FF9800; */ /* 후보 */
  background: #03a9f4;
}

.slide-seven {
  z-index: 2;
  /* background: #7f8c8d; */
  /* background: #d35400; */
  /* background: #FF9800; */ /* 후보 */
  /* background: #d2ff32; */
  /* /* background: #F1FF32/// #FFEB3B */

  /* background: #ff9800; */
  background: #fef100;

  /* background: #f1ff32; */
}

.slide-seven.on {
  z-index: 0;
  position: relative;
  /* background: #040404 */
}

.slide-four.bg .inbx{
  top: 5%;
}

.slide-four.on .inbx {
  top: 5%;
}

/* ******** (max-width: 1350px)************ */
@media screen and (max-width: 1480px) {
  /* 첫페이지 */
  .firstletterbx h2 {
    font-size: 60px;
  }
  .doubleafter {
    display: none;
  }
  .underlineafter {
    display: none;
  }
}
/* ******** (max-width: 1350px)************ */

/* ******** (max-width: 1200px)************ */
@media screen and (max-width: 1200px) {
  /* 첫페이지 */

  .firstletterbx h2 {
    font-size: 50px;
  }
  h4.firstletter {
    font-size: 20px;
  }

  .arrow,
  .influencer {
    display: none;
  }

  .firstletterbx.inbx {
    top: 30%;
  }

  /* 스와이퍼 페이지 */
  div.ourwork {
    text-align: center;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 3vw;
  }
  article.worktitle {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    gap: 3vw;
  }

  h2.subtit {
    font-size: 60px;
  }
  .atitaction h2.title.on {
    font-size: 60px;
  }
  .atitaction {
    height: 90px;
  }

  .text li {
    font-size: 20px;
  }

  .swiper {
    width: 250px;
    height: 350px;
  }

  /* 파트너 페이지  */

  .partnerexplain {
    margin-top: 0;
  }

  .partnerexplain .en {
    margin: 0;
    margin-bottom: 14px;
    font-size: 20px;
  }
  .partname {
    text-align: center;
  }
}

/* ******** (max-width: 1200px)************ */

/* ******** (max-width: 100px)************ */
@media screen and (max-width: 1000px) {
  /* 첫페이지 */
  .firstletterbx h2 {
    font-size: 40px;
  }
  h4.firstletter {
    font-size: 20px;
  }
  /* 2번째 페이지 */
  .letterbx article h3 {
    font-size: 30px;
  }

  /* 3번째 페이지 */
  .logoletter h5 {
    font-size: 60px;
  }
}

