.letterbx article h3 {
  font-family: 'Arial', dotum, san-serif;
  font-size: 50px;
  font-weight: 600;
  line-height: 1.5;
}

.mark.on.color0 {
  background-image: linear-gradient(#c7f3b3 0%, #44ff00 50%, #c7f3b3 100%);
}

.mark.on.color1 {
  background-image: linear-gradient(#e1fffe 0%, #03eaff 50%, #e1fffe 100%);
}

.mark.on.color2 {
  background-image: linear-gradient(#f3efb3 0%, #fffb00 50%, #f3efb3 100%);
}

.mark.on.color3 {
  background-image: linear-gradient(#f3dfb3 0%, #f5af1a 50%, #f3dfb3 100%);
}

.mark.on {
  display: inline-block;
  background-repeat: no-repeat;
  background-size: 0% 100%;
  /* animation: name duration timing-function delay iteration-count direction fill-mode; */
  animation: mark 0.2s cubic-bezier(0.68, -0.55, 0.27, 1.55) 0.2s forwards;
}

@keyframes mark {
  to {
    background-size: 100% 100%;
    color: #191919;
  }
}

